import React, { useContext, useEffect } from "react";
import {SubmitBtn,BottonBox,SelectedEmoji,SelectedEmojiBox,EmoticonSelectBox,Emoji, EmojiTaskBox } from '../../Styles/TaskStyle';
import {TaskContext} from "../TaskContext"

const EmotionTask = ({setFragmentState}) =>{

    const [selectedEmoji, setSelectedEmoji] = React.useState(false);
	const taskData = useContext(TaskContext);

	const setNextFragment = () =>{
		setFragmentState(4)
	}

    const btnPressed = (event) =>{
        //rename
        const {textContent : emotion} = event.target;
        setSelectedEmoji(emotion)
        // console.log(emotin);
    }

    useEffect(()=>{
        taskData.setEmoticon(selectedEmoji)
        // console.log(selectedEmoji)
    },[selectedEmoji]);

    return(
        // <div>hello</div>
    <>
        <EmojiTaskBox>
            <SelectedEmojiBox role="img" aria-label="sheep">
                <SelectedEmoji role="img" aria-label="sheep">
                    {selectedEmoji}
                </SelectedEmoji>
            </SelectedEmojiBox>

            <EmoticonSelectBox>
                <Emoji onClick={btnPressed}><span role="img" aria-label="amusement">😀</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="anger">😡</span></Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="concentration">🤔</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="contentment">☺️</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="desire">😋</span></Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="disappoinment">😞</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="elation">🤣</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="pain">😫</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="sadness">😢</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="surprise">😲</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="triumph">🥳</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="fear">😨</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="love">😍</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="neutral">😐</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="tired">😪</span> </Emoji>
                <Emoji onClick={btnPressed}><span role="img" aria-label="sick">🤒</span> </Emoji>
            </EmoticonSelectBox>
        </EmojiTaskBox>
        <BottonBox>
            <SubmitBtn onClick={setNextFragment}>다음</SubmitBtn>
        </BottonBox>
       </>
    )
};

export default EmotionTask;