import React, {useState,useContext, useEffect} from "react";
import { TaskContext } from "../TaskContext";
import { BottonBox,StateTypeContentLong,StateTypeContentShort,StateTypeWrapper,StateTypeContent,ProfileBox,SelectedStateBox,StateBox,StateInnerBox,StateType,SubmitBtn, SummaryBox, UserBox} from '../../Styles/TaskStyle';
import {FaUserCircle} from "react-icons/fa";



const TaskSummary = ({submitTaskData}) =>{
    const taskData = useContext(TaskContext)

    const relativenessToComponent = (relatedDegree) =>{
		if(relatedDegree<20){
            return(
                <StateTypeContent>
                    연관없음 
                </StateTypeContent>
            )
		}
		else if(relatedDegree<40){
            return(
                <StateTypeContentLong>
                    다소 연관없음
                </StateTypeContentLong>
            )

		}
		else if(relatedDegree<60){
            return(
                <StateTypeContent>
                    평범함
                </StateTypeContent>
            )

		}
		else if(relatedDegree<80){
            return(
                <StateTypeContentLong>
                    다소 연관있음 
                </StateTypeContentLong>
            )

		}
		else{
            return(
                <StateTypeContent>
                    연관있음
                </StateTypeContent>
            )
		}
    }

    const positivenessToComponent = (positiveDegree) =>{
		if(positiveDegree<20){
            return(
                <StateTypeContent>
                    부정적
                </StateTypeContent>
            )
		}
		else if(positiveDegree<40){
            return(
                <StateTypeContentLong>
                    다소 부정적
                </StateTypeContentLong>
            )

		}
		else if(positiveDegree<60){
            return(
                <StateTypeContentShort>
                    보통
                </StateTypeContentShort>
            )

		}
		else if(positiveDegree<80){
            return(
                <StateTypeContentLong>
                    다소 긍정적
                </StateTypeContentLong>
            )

		}
		else{
            return(
                <StateTypeContent>
                    긍정적
                </StateTypeContent>
            )
		}
    }


    return(
        <>
            <SummaryBox>
                <StateBox>
                    <SelectedStateBox>
                        <StateType>
                            긍정도
                        </StateType>
                        <StateTypeWrapper>
                            {positivenessToComponent(taskData.getPositiveDegree())}
                        </StateTypeWrapper>
                            {/* <h1>연관도</h1> */}

                    </SelectedStateBox>
                    <SelectedStateBox>
                        <StateType>
                            연관도
                        </StateType>
                        <StateTypeWrapper>
                            {relativenessToComponent(taskData.getRelatedDegree())}
                            {/* <StateTypeContentLong>
                                다소 평범함
                            </StateTypeContentLong> */}
                        </StateTypeWrapper>
                    </SelectedStateBox>
                    <SelectedStateBox>
                        <StateType>
                            감정
                        </StateType>
                        <StateTypeWrapper>
                            <StateTypeContentShort>
                                {taskData.getEmoticon()}
                            </StateTypeContentShort>
                            {/* <StateTypeContent>
                                다소높음
                            </StateTypeContent> */}
                        </StateTypeWrapper>
                    </SelectedStateBox>

                </StateBox>
                {/* <h2>김땡떙</h2>
                <h5>2020.04.03</h5>
                <h5>오후 4시 24분</h5> */}

            </SummaryBox>
            <BottonBox>
                <SubmitBtn onClick={submitTaskData}>제출 하기</SubmitBtn>
            </BottonBox>
        </>
    )

}

export default TaskSummary;