import React, {useCallback,useRef, useContext,useEffect, useState} from "react";
import {BackBtn,Question,StanbyBox , Outline, TaskBox,TaskCard, SubmitBtn, BottonBox, QuestionBox, BackBox, backText} from "../Styles/TaskStyle"
import PositiveTask from "./TaskFragment/PositiveTask";
import RecrodTask from "./TaskFragment/RecordTask";
import EmotionTask from "./TaskFragment/EmotionTask";
import { TaskContext } from "./TaskContext";
import { authService, uploadRecordFile, uploadTaskData } from "../Firebase/firebase";
import TaskSummary from "./TaskFragment/TaskSummary";
import RelativenessTask from "./TaskFragment/RelativenessTask";
import { IoIosArrowBack} from "react-icons/io";

// const RecrodTask = () =>{
//     const taskData = useContext(TaskContext);

const TaskSwitcher = ({userObj}) =>{
    const taskData = useContext(TaskContext)
    const [currentFragment,setCurrentFragment] = useState(0);

    const setCurrentFragmentFromChild = (fragment) =>{
        setCurrentFragment(fragment);
    }

    const decreaseFragment = useCallback(
        ()=>{
            setCurrentFragment(currentFragment-1);
            console.log(currentFragment);
        },
        [currentFragment]
    )
      
      

    useEffect(()=>{
        taskData.setUid(authService.currentUser.uid);
    },[])

    useEffect(() => {
        if(currentFragment < 5) return;
        window.parent.postMessage('TASK_FINISHED', '*');
    },[currentFragment])

    const useFadeIn = (duration= 1) =>{
        // if(typeof(duration)!=="number"){
        //     return;
        // }
        const element = useRef();
        useEffect(()=>{
            if(element.current){
                const {current} = element;
                current.style.transition = `opacity ${duration}s`;
                current.style.opacity = 1;
            }
        },[]);

        return{ref:element, style:{opacity:0}};
    }

    const fadeInTask = useFadeIn(4);



    // change current fragment
    const handleBtnOnClick = useCallback(
        ()=>{
        setCurrentFragment(currentFragment+1);
        console.log(currentFragment);
        },
        [currentFragment]
    )

    //formatting collected data in firestore form
    // const submitTaskDataFormatter = () =>{
    //     return new Promise(function(resolve,reject){
    //         uploadRecordFile(taskData.getBlob()).then((url)=>{
    //             console.log(url)
    //             taskData.setRecordFileURL(url)
    //             resolve(true)
    //         })
    //     })
    // }

    const setRecordURL = (url) =>{
        return new Promise(function(resolve,reject){
            taskData.setRecordFileURL(url)
            resolve(taskData.setRecordFileURL(url))
        })

    }

    //usecallback 를 사용하면 rerender를 하지 않아서 값이 초기값만 출력된다.
    const submitTaskData = ()=>{
        taskData.setUpdateTime();
        taskData.setRecordFileURLandUpload();
        setCurrentFragment(5);
    }


    return(
            <TaskCard>
                {/* <h1 {...fadeInTask}>gg</h1> */}
                <QuestionBox>
                    {currentFragment===0?(
                        <>
                        <Question>오늘의 이야기를 들려주세요</Question>
                        </>
                    ):
                    currentFragment===1?(
                        <>
                        <BackBtn>
                            <IoIosArrowBack onClick={decreaseFragment} size="35"/>
                        </BackBtn>
                        <Question>긍정적인 이야기인가요?</Question>
                        </>
                    ):
                    currentFragment===2?(
                        <>
                        <BackBtn>
                            <IoIosArrowBack onClick={decreaseFragment} size="35"/>
                        </BackBtn>
                        <Question>당신과 관련이 있나요?</Question>
                        </>
                    ):
                    currentFragment===3?(
                        <>
                        <BackBtn>
                            <IoIosArrowBack onClick={decreaseFragment} size="35"/>
                        </BackBtn>
                        <Question>지금의 감정은 어때요?</Question>
                        </>
                    ):
                    currentFragment===4?(
                        <>
                        <BackBtn>
                            <IoIosArrowBack onClick={decreaseFragment} size="35"/>
                        </BackBtn>
                        <Question>감정 상태 제출하기</Question>
                        </>
                    ):
                    null

                    }
                </QuestionBox>
                <TaskBox>
                    {currentFragment===0 ?(
                    <RecrodTask setFragmentState={setCurrentFragment}/>
                    ) : 
                    currentFragment===1 ?(
                    <PositiveTask setFragmentState={setCurrentFragment}/>
                    ) :
                    currentFragment===2 ?(
                    <RelativenessTask setFragmentState={setCurrentFragment}/>
                    ) :
                    currentFragment===3 ?(
                    <EmotionTask setFragmentState={setCurrentFragment}/>
                    ) :
                    currentFragment===4 ?(
                    <TaskSummary submitTaskData={submitTaskData}/>
                    ):(
                        <>
                        <Question>이제 다이어리를 기록해주세요</Question>
                        </>
                    )
                    }
                </TaskBox>
            </TaskCard>
    )

};

export default TaskSwitcher;