import firebase from "firebase/app"
import "firebase/auth"
import "firebase/firestore"
import "firebase/storage"
import firebaseConfig from "./FirebaseConfig"

firebase.initializeApp(firebaseConfig)

export const firebaseInstance = firebase;
export const authService = firebase.auth();
export const fbDatabase = firebase.firestore();
export const storageRef = firebase.storage().ref();

export const uploadTaskData = (task) =>{
    fbDatabase.collection("EmotionFrame/"+String(authService.currentUser.uid)+"/tasks").add({
        task
    }).then(function(docRef){
        console.log("Document written with ID: ", docRef.id);
    }).catch(function(error){
        console.error("Error adding document: ",error);
    })
}


export const uplaodLogData = (log) =>{
    fbDatabase.collection("EmotionFrame/"+String(authService.currentUser.uid)+"/LOG").add({
        log
    }).then(function(docRef){
        console.log("Document written with ID: ", docRef.id);
    }).catch(function(error){
        console.error("Error adding document: ",error);
    })
}

//Upload Record File and Return Its' Storage URL
export const uploadRecordFile = (blob) =>{
    return new Promise(function(resolve,reject){
        const recordsRef = storageRef.child(`webTask ${Date.now().toString()}`);
        recordsRef.put(blob).then(function(snapshot){
            snapshot.ref.getDownloadURL().then(function(url){
                resolve(url)
            }).catch(function(error){
                reject(error.code)
            })
        });

    })
}

export const signUp = (email,password) =>{
    return new Promise(function(resolve,reject){
        authService.createUserWithEmailAndPassword(email,password)
        .then((userCredential)=>{
            resolve(userCredential.user)
        })
        .catch((error)=>{
            reject(error)
        })
    })
}

export const signIn = (email,password)=>{
    return new Promise(function(resolve,reject){
        authService.signInWithEmailAndPassword(email,password)
        .then((userCredential)=>{
            resolve(userCredential.user)
        })
        .catch((error)=>{
            reject(error)
        })
    })
}






