import React, { useState, useContext, useRef, useEffect } from "react";
import { TaskContext } from "../TaskContext";
import {
  BottonBox,
  RangeSlider,
  SliderRightValue,
  RangeSliderBox,
  SliderField,
  SliderLeftValue,
  SliderValueBox,
  SliderValue,
  SliderTaskBox,
  SubmitBtn,
} from "../../Styles/TaskStyle";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import { Slider } from "@material-ui/core";
import ValueLabel from "./ValueLabel";
// import {Range} from "../../Styles/TaskStyle";

const PositiveTask = ({ setFragmentState }) => {
  const taskData = useContext(TaskContext);
  const [positiveDegree, setPositiveDegree] = useState(50);


  const setNextFragment = () => {
    setFragmentState(2);
  };

  useEffect(() => {
    taskData.setPositiveDegree(positiveDegree);
  }, [positiveDegree]);

  const mark = [
    {
      value: 0,
      label: "부정적",
    },
    {
      value: 50,
      label: "보통",
    },
    {
      value: 100,
      label: "긍정적",
    },
  ];

  const PrettoSlider = withStyles({
    root: {
      color: "#664aff",
      height: 6,
      flex: 1,
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    thumb: {
      height: 30,
      width: 30,
      backgroundColor: "#fff",
      top: 120,
      marginTop: -8,
      marginLeft: -12,
      "&:focus, &:hover, &$active": {
        boxShadow: "inherit",
      },
    },
    active: {},
    valueLabel: {
      //   left: 'calc(-50% + 4px)',
      left: -60,
      top: -100,
    },
    track: {
      height: 3,
      borderRadius: 7,
    },
    markLabel: {
      color: "#fff",
      position: "absolute",
      top: -20,
      transform: "translateX(-50%)",
      whiteSpace: "nowrap",
      "$vertical &": {
        top: "auto",
        left: 26,
        width: 100,
        transform: "translateY(50%)",
      },
      "@media (pointer: coarse)": {
        top: 40,
        "$vertical &": {
          left: 31,
        },
      },
    },
    rail: {
      height: 3,
      backgroundColor: "#fff",
      borderRadius: 7,
    },
  })(Slider);

  const PositivenessLabelValue = (value) => {
    if (value < 20) {
      return "부정적";
    } else if (value < 40) {
      return "다소 부정적";
    } else if (value < 60) {
      return "보통";
    } else if (value < 80) {
      return "다소 긍정적";
    } else {
      return "긍정적";
    }
  };


  return (
    <>
      <SliderTaskBox>
        <SliderField>

          <PrettoSlider
            aria-label="hello"
            onChangeCommitted={(_, value) => {
              console.log(value);
              setPositiveDegree(value);
            }}
            ValueLabelComponent={ValueLabel}
            defaultValue={positiveDegree}
            valueLabelFormat={PositivenessLabelValue}
            valueLabelDisplay="auto"
          />
        </SliderField>

      </SliderTaskBox>

      <BottonBox>
        <SubmitBtn onClick={setNextFragment}>다음</SubmitBtn>
      </BottonBox>
    </>
  );
};

export default PositiveTask;
