import React, { useContext, useEffect } from "react";
import Webcam from "react-webcam";
import { uploadRecordFile } from "../../Firebase/firebase";
import { TaskContext } from "../TaskContext";
import {HalfSubmitBtn,BottonBox, RecordBox, SubmitBtn} from "../../Styles/TaskStyle"

const RecrodTask = ({setFragmentState}) =>{
    const taskData = useContext(TaskContext);
    const webcamRef = React.useRef(null);
    const mediaRecorderRef = React.useRef(null);
    const [capturing, setCapturing] = React.useState(false);
    const [first, setFirst] = React.useState(true);
    const [recordedChunks, setRecordedChunks] = React.useState([]);
    const [blob,setBlob] = React.useState('');


  
    const handleStartCaptureClick = React.useCallback(() => {
      setFirst(false)
      setCapturing(true);
      mediaRecorderRef.current = new MediaRecorder(webcamRef.current.stream, {
        mimeType: "video/webm"
      });
      mediaRecorderRef.current.addEventListener(
        "dataavailable",
        handleDataAvailable
      );
      mediaRecorderRef.current.start();
    }, [webcamRef, setCapturing, mediaRecorderRef,blob]);
  
    const handleDataAvailable = React.useCallback(
      ({ data }) => {
        if (data.size > 0) {
          setRecordedChunks((prev) => prev.concat(data));
        }
      },
      [setRecordedChunks]
    );

  
    const handleStopCaptureClick = React.useCallback(() => {
      mediaRecorderRef.current.stop();
      setCapturing(false);
      // handleDownload();
    }, [mediaRecorderRef, webcamRef, setCapturing]);
    
    const handleDownload = React.useCallback(() => {
      if (recordedChunks.length) {
        const blob = new Blob(recordedChunks, {
          type: "video/webm"
        });
        taskData.setBlob(blob)
        setRecordedChunks([]);
        setFragmentState(1)
      }
    }, [recordedChunks,blob]);


    return(
        <>
          <RecordBox>
            <Webcam className="TaskCam" mirrored="true" audio={true} width={600} style={{ transform: 'rotate(90deg)'}} ref={webcamRef}/>
          </RecordBox>
          <BottonBox>
            {first && (
              <SubmitBtn onClick={handleStartCaptureClick}>녹화 시작</SubmitBtn>
            )}
            {capturing && (
              <SubmitBtn onClick={handleStopCaptureClick}>녹화 완료</SubmitBtn>
            )}
            {!capturing && !first && recordedChunks.length>0 &&(
              <>
                <HalfSubmitBtn style={{
                  background:"#efefef",
                  color:"#375abd"
                }} onClick={handleStartCaptureClick}>재녹화</HalfSubmitBtn>
                <HalfSubmitBtn onClick={handleDownload}>다음</HalfSubmitBtn>
              </>

            )}

          </BottonBox>
        </>
    )
}

export default RecrodTask;